import { MobileRedoButton, MobileUndoButton } from '../components/mobileButton';
import type { HistoryPluginConfig } from '../types';

export default function toolbar(historyConfig: HistoryPluginConfig) {
  const historyLocaleConfig = historyConfig.locale || {};

  return {
    buttons: {
      undoMobile: MobileUndoButton,
      redoMobile: MobileRedoButton,
    },
    locale: historyLocaleConfig,
  };
}
